@font-face {
  font-family: "TT Lakes Regular";
  src: url(./fonts/TTLakes-Regular.woff) format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "TT Lakes Medium";
  src: url(./fonts/TTLakes-Medium.woff) format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "TT Lakes Bold";
  src: url(./fonts/TTLakes-Bold.woff) format("woff");
  font-weight: normal;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background-color: #f8f8f8;
  font-family: "TT Lakes Regular", sans-serif;
  min-width: 320px;
  overflow-x: hidden;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}

.container-main {
  max-width: 1120px;
  margin: 0 auto;
  padding: 0 16px;
}

.container-main-blur {
  opacity: 0.1;
}

.body-overflow-hidden {
  overflow: hidden;
}

@media (min-width: 768px) {
  .container-main {
    padding: 0 24px;
  }
}
@media (min-width: 1440px) {
  .container-main {
    margin: 0 0 0 auto;
    padding: 0 32px;
  }
}
.header__nav-container {
  position: sticky;
  top: 0;
  margin: 0 -24px;
  padding: 0 24px;
  background-color: #ffffff;
}
.header__nav {
  min-height: 88px;
  display: flex;
  gap: 16px;
  justify-content: space-between;
  align-items: center;
}
.header__nav button {
  width: 40px;
  height: 40px;
  border: none;
}
.header__logo-btn {
  min-width: 96.55px;
  min-height: 50.81px;
  background: url(./static/logo.svg);
}
.header__burger-btn {
  background: url(./static/burger.svg);
}
.header__call-btn {
  background: url(./static/call.svg);
}
.header__chat-btn {
  background: url(./static/chat.svg);
}
.header__profile-btn {
  background: url(./static/profile.svg);
}
.header__repair-btn {
  background: url(./static/repair.svg);
}
.header__checkstatus-btn {
  background: url(./static/checkstatus.svg);
}
.header__left-wrapper {
  display: flex;
  gap: 17px;
}
.header__left-divider {
  display: none;
  min-width: 2px;
  min-height: 32px;
  background-color: #eaeaea;
}
.header__right-divider {
  min-width: 2px;
  min-height: 32px;
  background-color: #eaeaea;
}
.header__icons-container {
  display: flex;
  gap: 20px;
}
.header__tablet-icons {
  display: none;
}
.header__main-icons {
  display: flex;
  gap: 16px;
}

@media (min-width: 768px) {
  .header {
    position: static;
  }
  .header__left-wrapper {
    display: flex;
  }
  .header__left-divider {
    display: block;
  }
  .header__right-divider {
    position: static;
  }
  .header__icons-container {
    display: flex;
    gap: 20px;
  }
  .header__tablet-icons {
    display: flex;
    gap: 16px;
  }
}
@media (min-width: 1440px) {
  .header {
    display: none;
  }
}
.main__repair-btn .img {
  min-width: 40px;
  min-height: 40px;
  background: url(./static/repair.svg);
}
.main__checkstatus-btn .img {
  min-width: 40px;
  min-height: 40px;
  background: url(./static/checkstatus.svg);
}
.main__icons-inner {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
}
.main__icons {
  display: none;
}
.main__h1-wrapper {
  position: relative;
  margin-top: 24px;
}
.main__highlight {
  background-color: #41f6d7;
  width: 8px;
  height: 40px;
  border-radius: 4px;
  position: absolute;
  left: -20px;
}
.main__h1 {
  font-family: "TT Lakes Bold";
  font-size: 28px;
  text-align: left;
}
.main__slide-menu {
  margin-top: 24px;
  white-space: nowrap;
  overflow-x: scroll;
}
.main__slide-menu::-webkit-scrollbar {
  display: none;
}
.main__slide-menu .item {
  background-color: #f8f8f8;
  padding: 8px 9px 8px 16px;
  border: 2px solid #f8f8f8;
  font-size: 16px;
  line-height: 24px;
  text-align: left;
  color: #7e7e82;
}
.main__slide-menu .item--hide {
  display: none;
}
.main__slide-menu .item:hover {
  border: 2px solid #b8ffec;
  border-radius: 6px;
}
.main .show {
  display: block;
}

@media (min-width: 768px) and (max-width: 1440px) {
  .main__highlight {
    left: -28px;
  }
}
@media (min-width: 1440px) {
  .main {
    margin-left: auto !important;
  }
  .main__services {
    margin-top: 20px;
    padding-top: 0;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
  }
  .main__slide-menu {
    display: flex;
    flex-wrap: wrap;
  }
  .main__h1-wrapper {
    margin: 0;
  }
  .main__highlight {
    left: -36px;
    top: 15px;
  }
  .main__icons {
    display: flex;
    gap: 32px;
    font-weight: bold;
  }
  .main__icons a {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    text-decoration: none;
    color: #1b1c21;
  }
}
.info__btn-wrapper {
  display: flex;
  align-items: center;
  gap: 2px;
}
.info__btn-arrows {
  min-width: 24px;
  min-height: 24px;
  transition: transform 0.3s ease;
  background: url(./static/expand.svg);
  background-repeat: no-repeat;
}
.info__btn {
  color: #1b1c21;
  background-color: #f8f8f8;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  border: none;
  cursor: pointer;
  position: relative;
}
.info__picture {
  margin-top: 16px;
  width: 100%;
}
.info__text {
  font-size: 14px;
  color: #1b1c21;
}
.info__p2, .info__p3, .info__p4 {
  display: none;
}
.info .rotate {
  transform: rotate(180deg);
}

@media (min-width: 768px) {
  .info {
    display: flex;
    gap: 48px;
  }
  .info__text {
    color: #1b1c21;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 142.857% */
    letter-spacing: 0.2px;
    max-width: 312px;
    align-self: flex-start;
  }
}
@media (min-width: 768px) and (max-width: 1440px) {
  .info {
    gap: 48px;
  }
  .info__p2 {
    display: block;
  }
  .info__picture {
    border-radius: 5px;
    max-height: 208px;
    max-width: 360px;
  }
}
@media (min-width: 1440px) {
  .info__p1, .info__p3 {
    display: block;
  }
  .info__p2 {
    display: none;
  }
  .info__text {
    max-width: 490px;
    align-self: flex-start;
  }
  .info__picture {
    height: 383px;
    width: 530px;
  }
}
.brands {
  overflow: hidden;
  margin-top: 15px;
}
.brands__title-wrapper {
  text-transform: uppercase;
  padding: 15px 16px;
  background: linear-gradient(270deg, #fff 0%, #f8f8f8 100%);
}
.brands__title-text {
  font-family: "TT Lakes", sans-serif;
  line-height: 24px;
  font-size: 16px;
  font-weight: 400;
  padding: 0;
  margin: 0;
  color: #7e7e82;
}
.brands__item {
  justify-content: space-between;
  max-width: 240px;
  align-items: center;
  background-color: white;
  padding: 12px 16px;
  border-radius: 6px;
  border: 1px solid #eaeaea;
  width: 240px;
  height: 72px;
}
.brands__img {
  max-width: 128px;
  max-height: 44px;
}
.brands__link {
  display: inline-block;
  background: url(./static/go.svg);
  width: 40px;
  height: 40px;
  cursor: pointer;
}
.brands .swiper-slide {
  display: flex;
}
.brands .swiper-pagination {
  position: static;
  margin-top: 16px;
}
.brands .btn-wrapper {
  display: none;
}

@media (min-width: 768px) {
  .brands .btn-wrapper {
    display: flex;
  }
  .brands .btn-wrapper__expand {
    align-self: flex-end;
    top: 40px;
    background: url(./static/expand.svg);
    margin-top: 20px;
    width: 18px;
    height: 28px;
    transition: transform 0.3s ease;
  }
  .brands .btn-wrapper__hide-show {
    margin-top: 20px;
    margin-left: 10px;
    padding: 0;
    border: none;
    background: none;
    cursor: pointer;
    align-items: center;
    color: #1b1c21;
    white-space: nowrap;
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    color: #7e7e82;
  }
  .brands .rotate {
    transform: rotate(180deg);
  }
}
@media (min-width: 768px) and (max-width: 1120px) {
  .brands .swiper-wrapper {
    display: grid;
    grid-template-columns: repeat(3, 224px);
    grid-gap: 24px;
  }
  .brands .hide-tablet-items:nth-child(n+7) {
    display: none;
  }
  .brands .show-tablet-items:nth-child(n+7) {
    display: flex;
  }
}
@media (min-width: 1120px) {
  .brands .swiper-wrapper {
    display: grid;
    grid-template-columns: repeat(4, 240px);
    grid-gap: 32px;
  }
  .brands .hide-desktop-items:nth-child(n+9) {
    display: none;
  }
  .brands .show-desktop-items:nth-child(n+9) {
    display: flex;
  }
}
